
.header {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  color: #fff;
  width: 100%;
  min-height: 50px;
  font-size: 16px;
  z-index: 10;
  line-height: 50px;
  background: #03a9f4;
  text-align: center;
  display: table;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1), 0 1px 2px rgba(0, 0, 0, 0.1);
  transition: all .3s ease;
}
.header.show {
    -webkit-transform: translate(250px, 0);
            transform: translate(250px, 0);
}
.header .title {
    min-height: 50px;
    position: absolute;
    margin: 0;
    left: 100px;
    right: 100px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: auto;
    text-align: center;
    font-weight: 500;
    z-index: 2;
}
.header .iconfont {
    font-size: 18px;
}
.header .pull-left, .header .pull-right {
    padding: 0 10px;
    font-weight: 400;
    z-index: 2;
    box-sizing: border-box;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    display: -webkit-flex;
    float: left !important;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.header .pull-right {
    float: right !important;
}



